import React, {
  useState,
  ReactNode,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'sm' | 'md' | 'lg';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size,
  style,
  onClick
}: ButtonProps) => {

  const defaultStyles = {
    border: '1px solid #ccc',
    borderRadius: '3px',
  } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        !style ? defaultStyles : style
      }
    >
      {children}
    </button>
  )
}

export default Button;
